* {
	margin:0;
	padding:0;
	font-family: 'Fjalla One', sans-serif;
}

body{
	color:#fff;
	font-size:18px;
	background: #eaeaea;
}

.button{
	width:200px;
	height:200px;
	border-radius: 50%;
	-webkit-box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	        box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	z-index:1000;
	background:#fff;
	cursor:pointer;
	font-size: 30px;
	color: #fff;
	outline: none;
	font-family: 'Fjalla One', sans-serif;
	background-color: #f24343;
	margin-left: 35px;
	margin-top: 10px;
}

.small-button{
	width:40px;
	height:40px;
	border-radius: 50%;
	-webkit-box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	        box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	z-index:1000;
	background:#fff;
	cursor:pointer;
	font-size: 14px;
	color: #fff;
	outline: none;
	font-family: 'Fjalla One', sans-serif;
	background-color: #f24343;
	margin: 10px;
}

.med-button{
	width:75px;
	height:75px;
	border-radius: 50%;
	-webkit-box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	        box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	z-index:1000;
	background:#fff;
	cursor:pointer;
	font-size: 14px;
	color: #fff;
	outline: none;
	font-family: 'Fjalla One', sans-serif;
	background-color: #f24343;
	margin: 15px;
	margin-left: 70px;
}

.input {
	width: 75%;
	padding: 12px 20px;
	margin: 8px 0;
	display: inline-block;
	border: 1px solid #ccc;
	border-radius: 4px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	outline: none;
	font-size: 18px;
}

.player {
	padding: 5px;
}

.list{
	margin: 10px;
	color: #999;
}

.task{
	color: #999;
	margin: 15px auto 0;
	border: 4px solid rgba(0,0,0,0.1);
	background-color: #fff;
	width:242px;
	height:242px;
	border-radius: 10px;
	padding: 10px;
	height: auto;
}

.task-title{
	color: black;
}

/*WRAPPER*/
#wrapper{
	margin: 40px auto 0;
	width:266px;
}

#txt{
	color:#eaeaea;
}


/*WHEEL*/
#wheel{
	width:250px;
	height:250px;
	border-radius:50%;
	position:relative;
	overflow:hidden;
	border:8px solid #fff;
	-webkit-box-shadow:rgba(0,0,0,0.2) 0px 0px 10px, rgba(0,0,0,0.05) 0px 3px 0px;
	        box-shadow:rgba(0,0,0,0.2) 0px 0px 10px, rgba(0,0,0,0.05) 0px 3px 0px;
	-webkit-transform: rotate(0deg);
	    -ms-transform: rotate(0deg);
	        transform: rotate(0deg);
}

#wheel:before{
	content:'';
	position:absolute;
	border:4px solid rgba(0,0,0,0.1);
	width:242px;
	height:242px;
	border-radius:50%;
	z-index:1000;
}

#inner-wheel{
	width:100%;
	height:100%;

	-webkit-transition: all 6s cubic-bezier(0,.99,.44,.99);
	-o-transition:      all 6s cubic-bezier(0,.99,.44,.99);
	transition:         all 6s cubic-bezier(0,.99,.44,.99);
}

#wheel div.sec{
	position: absolute;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 130px 75px 0;
	border-color: #19c transparent;
	-webkit-transform-origin: 75px 129px;
	    -ms-transform-origin: 75px 129px;
	        transform-origin: 75px 129px;
	left:50px;
	top:-4px;
	opacity:1;
	font-family: 'Fjalla One', sans-serif;

}

#wheel div.sec:nth-child(1){
	transform: rotate(60deg);
	-webkit-transform: rotate(60deg);
	-moz-transform: rotate(60deg);
	-o-transform: rotate(60deg);
	-ms-transform: rotate(60deg);
	border-color: #b74edb transparent;
}
#wheel div.sec:nth-child(2){
	transform: rotate(120deg);
	-webkit-transform: rotate(120deg);
	-moz-transform: rotate(120deg);
	-o-transform: rotate(120deg);
	-ms-transform: rotate(120deg);
	border-color: #4c7aef transparent;
}
#wheel div.sec:nth-child(3){
	transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
	-moz-transform: rotate(180deg);
	-o-transform: rotate(180deg);
	-ms-transform: rotate(180deg);
	border-color: #3faa4b transparent;
}
#wheel div.sec:nth-child(4){
	transform: rotate(240deg);
	-webkit-transform: rotate(240deg);
	-moz-transform: rotate(240deg);
	-o-transform: rotate(240deg);
	-ms-transform: rotate(240deg);
	border-color: #ffcb11 transparent;
}
#wheel div.sec:nth-child(5){
	transform: rotate(300deg);
	-webkit-transform: rotate(300deg);
	-moz-transform: rotate(300deg);
	-o-transform: rotate(300deg);
	-ms-transform: rotate(300deg);
	border-color: #ff7f00 transparent;
}
#wheel div.sec:nth-child(6){
	transform: rotate(360deg);
	-webkit-transform: rotate(360deg);
	-moz-transform: rotate(360deg);
	-o-transform: rotate(360deg);
	-ms-transform: rotate(360deg);
	border-color: #f24343 transparent;
}

#wheel div.sec .fa{
	margin-top: -100px;
	color: #fff;
	position: relative;
	z-index: 10000000;
	display: block;
	text-align: center;
	font-size:36px;
	margin-left:-18px;
}

#spin{
	width:68px;
	height:68px;
	position:absolute;
	top:50%;
	left:50%;
	margin:-34px 0 0 -34px;
	border-radius:50%;
	-webkit-box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	        box-shadow:rgba(0,0,0,0.1) 0px 3px 0px;
	z-index:1000;
	background:#fff;
	cursor:pointer;
	font-family: 'Fjalla One', sans-serif;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;
}

#spin:after{
	content:"SPONK";
	text-align:center;
	line-height:68px;
	color:#CCC;
	position: relative;
	z-index: 100000;
	width:68px;
	height:68px;
	display:block;
}

#spin:before{
	content:"";
	position:absolute;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 0 20px 28px 20px;
	border-color: transparent transparent #ffffff transparent;
	top:-12px;
	left:14px;
}

#inner-spin{
	width:54px;
	height:54px;
	position:absolute;
	top:50%;
	left:50%;
	margin:-27px 0 0 -27px;
	border-radius:50%;
	background:red;
	z-index:999;
	-webkit-box-shadow:rgba(255,255,255,1) 0px -2px 0px inset, rgba(255,255,255,1) 0px 2px 0px inset,  rgba(0,0,0,0.4) 0px 0px 5px ;
	        box-shadow:rgba(255,255,255,1) 0px -2px 0px inset, rgba(255,255,255,1) 0px 2px 0px inset,  rgba(0,0,0,0.4) 0px 0px 5px ;
	background: rgb(255,255,255); /* Old browsers */ /* FF3.6+ */
	background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%,rgba(255,255,255,1)), color-stop(100%,rgba(234,234,234,1))); /* Chrome,Safari4+ */
	background: -webkit-radial-gradient(center, ellipse cover,  rgba(255,255,255,1) 0%,rgba(234,234,234,1) 100%); /* Chrome10+,Safari5.1+ */
	background: -o-radial-gradient(center, ellipse cover,  rgba(255,255,255,1) 0%,rgba(234,234,234,1) 100%); /* Opera 12+ */ /* IE10+ */
	background: -webkit-radial-gradient(center,  ellipse,  rgba(255,255,255,1) 0%,rgba(234,234,234,1) 100%);
	background: -o-radial-gradient(center,  ellipse,  rgba(255,255,255,1) 0%,rgba(234,234,234,1) 100%);
	background: radial-gradient(ellipse at center,  rgba(255,255,255,1) 0%,rgba(234,234,234,1) 100%); /* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#eaeaea',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

#spin:active #inner-spin{
	-webkit-box-shadow:rgba(0,0,0,0.4) 0px 0px 5px inset;
	        box-shadow:rgba(0,0,0,0.4) 0px 0px 5px inset;
}

#spin:active:after{
	font-size:15px;
}



#shine{
	width:250px;
	height:250px;
	position:absolute;
	top:0;
	left:0; /* FF3.6+ */
	background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%,rgba(255,255,255,1)), color-stop(1%,rgba(255,255,255,0.99)), color-stop(9%,rgba(255,255,255,0.91)), color-stop(100%,rgba(255,255,255,0))); /* Chrome,Safari4+ */
	background: -webkit-radial-gradient(center, ellipse cover,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.91) 9%,rgba(255,255,255,0) 100%); /* Chrome10+,Safari5.1+ */
	background: -o-radial-gradient(center, ellipse cover,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.91) 9%,rgba(255,255,255,0) 100%); /* Opera 12+ */ /* IE10+ */
	background: -webkit-radial-gradient(center,  ellipse,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.91) 9%,rgba(255,255,255,0) 100%);
	background: -o-radial-gradient(center,  ellipse,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.91) 9%,rgba(255,255,255,0) 100%);
	background: radial-gradient(ellipse at center,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.91) 9%,rgba(255,255,255,0) 100%); /* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */


	opacity:0.1;

}



/*ANIMATION*/
@-webkit-keyframes hh {
	0%, 100%{
		transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
	}

	50%{
		transform: rotate(7deg);
		-webkit-transform: rotate(7deg);
	}
}

@keyframes hh {
	0%, 100%{
		transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
	}

	50%{
		transform: rotate(7deg);
		-webkit-transform: rotate(7deg);
	}
}

.spin {
	-webkit-animation: hh 0.1s; /* Chrome, Safari, Opera */
	animation: hh 0.1s;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
}



